.form {
    height: 60px;
    display: flex;
    border: 1px solid rgba(25, 25, 25, 0.32);
}

.form_input {
    flex: 1;
    font-size: 16px;
    font-family: "Rubik";
    padding: 20px;
}

.attachment_button,
.location_button,
.audio_button,
.emoji_button {
    color: #757575;
    border: 0;
    background-color: white;
    cursor: pointer;
    transition: opacity 0.2s, color 0.2s;
}

.attachment_button {
    padding: 18px 10px 10px;
}

.location_button,
.audio_button,
.audio_button_on_record,
.emoji_button {
    padding-right: 10px;
    padding-left: 5px;
}

.audio_button_on_record {
    color: red;
    border: 0;
    background-color: white;
}

.attachment_button:hover,
.location_button:hover,
.audio_button:hover,
.emoji_button:hover {
    opacity: 0.8;
}
  
.attachment_button:active,
.location_button:active,
.audio_button:active,
.emoji_button:active {
    opacity: 0.8;
    color: #aa00ff;
}

input[type=submit] {
    visibility: collapse;
}

input {
    border: 0;
    outline: none;
    width: calc(100% - 2px);
}

.visually_hidden {
    position: absolute !important;
    height: 1px;
    width: 1px;
    overflow: hidden;
    clip: rect(1px, 1px, 1px, 1px);
}
  
input.visually_hidden:is(:focus, :focus-within) + label {
    outline: thin dotted;
}